@import "./variable.scss";
.project {
  width: 100vw;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  @media screen and (max-width: 480px) {
    display: flex;
    justify-content: flex-start;
    top: 20px;
    height: 80vh;
  }

  &__slider {
    height: 500px;
    display: flex;
    position: absolute;
    left: 0;
    &__container {
      width: 100vw;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      justify-items: center;
      align-content: center;
      &__item {
        width: 65vw;
        height: 100%;
        background-color: #fff;
        border-radius: 20px;
        display: grid;
        grid-gap: 10px;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
        align-items: center;
        justify-content: center;
        // flex-direction: row;
        .left {
          flex: 4;
          height: 100%;
          // background-color: yellowgreen;
          //   border-radius: 20px;
        }
        .right {
          flex: 8;
          height: 100%;
          // background-color: yellow;
        }
      }
    }
  }
}

